import React, { useEffect, useState } from "react"
import PropTypes from "prop-types"
import styled from "styled-components"

import tokens from "../_tokens"
import { ButtonInvisible, Icon, Link, NavItem } from "./"
import EditableInput from "./editable-input"
import preval from "preval.macro"
import { DragDropContext, Draggable, Droppable } from "react-beautiful-dnd"

// This code needs to run at build-time so it can access the file system.
const repositoryUrl = preval`
  const readPkgUp = require('read-pkg-up')
  const getPkgRepo = require('get-pkg-repo')
  try {
    const repo = getPkgRepo(readPkgUp.sync().package)
    module.exports = \`https://github.com/\${repo.user}/\${repo.project}\`
  } catch (error) {
    module.exports = ''
  }
`

const toKebabCase = str => {
  return str
    .replace(/([a-z])([A-Z])/g, "$1-$2") // get all lowercase letters that are near to uppercase ones
    .replace(/[\s_]+/g, "-") // replace all spaces and low dash
    .toLowerCase() // convert to lower case
}

const StyledRepoUrlContainer = styled.div`
  border: 0;
  border-top: ${0.5 * tokens.size.base}px;
  padding: ${2.5 * tokens.size.base}px;
`
const StyledNavContainer = styled.div`
  background-color: ${props =>
    props.isDraggingOver
      ? `${tokens.color.primaryTransparent}`
      : "transparent"};
`

const NavContainer = ({ children }) => (
  <Droppable droppableId="droppable" type="droppableItem">
    {(provided, snapshot) => (
      <StyledNavContainer
        ref={provided.innerRef}
        isDraggingOver={snapshot.isDraggingOver}
      >
        {children}
        {provided.placeholder}
      </StyledNavContainer>
    )}
  </Droppable>
)

NavContainer.propTypes = {
  children: PropTypes.node,
}

const StyledNavSection = styled.ul`
  margin: 0;
  background: ${tokens.color.white};
  padding: ${2.5 * tokens.size.base}px ${0.5 * tokens.size.base}px
    ${tokens.size.base}px ${2.5 * tokens.size.base}px;
  border-bottom: 1px solid ${tokens.color.border};
  font-size: ${tokens.fontSize.heading.m};

  border: ${props =>
    props.isDragging ? `solid 1px ${tokens.color.primary}` : "none"};
`
const StyledNavItemsList = styled.ul`
  margin: 0;
  background: ${props =>
    props.isDraggingOver
      ? tokens.color.primaryTransparent
      : tokens.color.white};
  padding-left: 0;
  border: ${props =>
    props.isDragging ? `solid 1px ${tokens.color.primary}` : "none"};
  font-size: ${tokens.fontSize.heading.s};
`

const ButtonAdd = ({ children, addItem, parentId, ...props }) => {
  const [isInputShown, setInputShown] = useState(false)

  const isValidInput = string => {
    return string && string.trim().length > 0 ? true : false
  }

  const handleInputEnd = text => {
    if (isValidInput(text)) {
      addItem(text, parentId)
    }
    setInputShown(false)
  }

  const handleClick = () => setInputShown(true)

  return (
    <EditableInput
      isInputVisible={isInputShown}
      handleInputEnd={handleInputEnd}
    >
      <ButtonInvisible {...props} onClick={handleClick}>
        <Icon name="plus" width="22" height="22" />
        <span>{children}</span>
      </ButtonInvisible>
    </EditableInput>
  )
}

ButtonAdd.propTypes = {
  addItem: PropTypes.func,
  children: PropTypes.node,
  parentId: PropTypes.string,
}

const NavSection = ({
  item,
  children,
  index,
  changeMenuItem,
  addItem,
  deleteItem,
}) => (
  <Draggable draggableId={item.url} index={index} key={item.url}>
    {(provided, snapshot) => (
      <>
        <StyledNavSection
          ref={provided.innerRef}
          {...provided.draggableProps}
          isDragging={snapshot.isDragging}
        >
          <NavItem
            isParent
            item={item}
            index={index}
            changeMenuItem={changeMenuItem}
            deleteItem={deleteItem}
            dragHandleProps={provided.dragHandleProps}
          />

          <Droppable
            droppableId={item.id}
            key={`NavItem__${item.id}`}
            type={`droppableChildItem`}
          >
            {(provided, snapshot) => (
              <>
                <StyledNavItemsList
                  ref={provided.innerRef}
                  isDraggingOver={snapshot.isDraggingOver}
                >
                  {children}
                </StyledNavItemsList>
                {provided.placeholder}
                <ButtonAdd
                  css={{ fontSize: tokens.fontSize.heading.s }}
                  addItem={addItem}
                  parentId={item.id}
                >
                  Add page
                </ButtonAdd>
              </>
            )}
          </Droppable>
        </StyledNavSection>
      </>
    )}
  </Draggable>
)

NavSection.propTypes = {
  addItem: PropTypes.func,
  changeMenuItem: PropTypes.func,
  deleteItem: PropTypes.func,
  children: PropTypes.node,
  index: PropTypes.number,
  item: PropTypes.shape({
    id: PropTypes.string,
    title: PropTypes.string,
    url: PropTypes.string,
  }),
}

function NavItems(props) {
  const [items, setItems] = useState([])

  useEffect(() => {
    setItems(props.items)
  }, props.items)

  function updateItems(items) {
    setItems(items)
    parent.postMessage({ type: "NAV_CHANGE", nav: items }, "*")
  }

  const changeItem = e => {
    let newItems = items.slice()
    // Change 1st level
    let firstLevelItem = newItems.find(el => el.id === e.target.id)
    if (firstLevelItem) {
      firstLevelItem.title = e.target.value
    } else {
      // Change 2nd levels
      newItems = newItems.map(item => {
        item.items =
          item.items &&
          item.items.map(child => {
            if (child.url === e.target.id) child.title = e.target.value
            return child
          })
        return item
      })
    }
    updateItems(newItems)
  }

  // TODO replace Math.random with normal generator of unique id
  const addItem = (name, parentId) => {
    let newItem = {
      title: name,
      url: toKebabCase(name),
      id: name + Math.random(),
    }
    let newItems = items.slice()
    if (parentId) {
      const parentItem = newItems.find(item => item.id === parentId)

      newItem = {
        title: name,
        url: toKebabCase(parentItem.title + "/" + name),
        id: name + Math.random(),
      }
      parentItem.items.push(newItem)
    } else {
      newItems.push(newItem)
    }
    updateItems(newItems)
  }

  const deleteItem = e => {
    let newItems = items.slice()
    newItems = newItems.reduce((res, item) => {
      if (item.items) {
        item.items = item.items.filter(s => {
          return s.url != e.target.id
        })
      }
      item.url != e.target.id && res.push(item)
      return res
    }, [])

    updateItems(newItems)
  }

  const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list)
    const [removed] = result.splice(startIndex, 1)
    result.splice(endIndex, 0, removed)

    return result
  }

  // from https://codesandbox.io/s/nested-dnd-across-parent-forked-seki3?file=/index.js:1438-3161
  const onDragEnd = result => {
    const { destination, source } = result
    if (!destination) return
    if (
      destination.droppableId === source.droppableId &&
      destination.index === source.index
    )
      return

    const sourceIndex = result.source.index
    const destIndex = result.destination.index
    if (result.type === "droppableItem") {
      const reorderedItems = reorder(items, sourceIndex, destIndex)
      updateItems(reorderedItems)
    } else if (result.type === "droppableChildItem") {
      const itemSubItemMap = items.reduce((acc, item) => {
        acc[item.id] = item.items
        return acc
      }, {})
      const sourceParentId = result.source.droppableId
      const destParentId = result.destination.droppableId
      const sourceSubItems = itemSubItemMap[sourceParentId]
      const destSubItems = itemSubItemMap[destParentId]
      let newItems = [...items]
      /** In this case  child items are reOrdered inside same Parent */
      if (sourceParentId === destParentId) {
        const reorderedSubItems = reorder(
          sourceSubItems,
          sourceIndex,
          destIndex
        )
        newItems = newItems.map(item => {
          if (item.id === sourceParentId) {
            item.items = reorderedSubItems
          }
          return item
        })
        updateItems(newItems)
      } else {
        let newSourceSubItems = [...sourceSubItems]

        const [draggedItem] = newSourceSubItems.splice(sourceIndex, 1)
        let newDestSubItems = [...destSubItems]

        newDestSubItems.splice(destIndex, 0, draggedItem)

        newItems = newItems.map(item => {
          if (item.id === sourceParentId) {
            item.items = newSourceSubItems
          } else if (item.id === destParentId) {
            item.items = newDestSubItems
          }
          return item
        })

        updateItems(newItems)
      }
    }
  }

  return (
    <>
      <DragDropContext onDragEnd={onDragEnd}>
        <NavContainer>
          {items.map((item, index) => (
            <NavSection
              item={item}
              index={index}
              key={item.url}
              changeMenuItem={changeItem}
              addItem={addItem}
              deleteItem={deleteItem.bind(this)}
            >
              {item.items &&
                item.items.map((child, index) => (
                  <NavItem
                    key={child.url + "navitem"}
                    index={index}
                    item={child}
                    changeMenuItem={changeItem.bind(this)}
                    deleteItem={deleteItem.bind(this)}
                  />
                ))}
            </NavSection>
          ))}
        </NavContainer>
      </DragDropContext>
      <ButtonAdd
        css={{
          fontSize: tokens.fontSize.heading.m,
          marginLeft: 2.5 * tokens.size.base,
          marginTop: 2 * tokens.size.base,
        }}
        addItem={addItem}
      >
        Add new section
      </ButtonAdd>
      {repositoryUrl ? (
        <StyledRepoUrlContainer>
          <Link href={repositoryUrl} style={{ color: "inherit" }}>
            GitHub
          </Link>
        </StyledRepoUrlContainer>
      ) : null}
    </>
  )
}

NavItems.propTypes = {
  items: PropTypes.array,
}

export default NavItems
