import React from "react"
import styled from "styled-components"

import tokens from "../_tokens"
import Link from "./link"

function SkipLinkBase(props) {
  return (
    <Link {...props} href="#skip-nav">
      Skip to content
    </Link>
  )
}

const SkipLink = styled(SkipLinkBase)`
  background-color: ${tokens.color.primary};
  color: ${3 * tokens.color.primary};
  padding: ${3 * tokens.size.base}px;
  font-size: ${tokens.fontSize.text.s};

  z-index: 20;
  width: auto;
  height: auto;
  clip: auto;
  position: absolute;
  overflow: hidden;

  // The following rules are to ensure that the element
  // is visually hidden, unless it has focus. This is the recommended
  // way to hide content from:
  // https://webaim.org/techniques/css/invisiblecontent/#techniques

  &:not(:focus) {
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
    height: 1px;
    width: 1px;
    margin: -1px;
    padding: 0;
  }
`

export default SkipLink
